import React, { useState } from 'react'
import Slider from 'react-touch-drag-slider'
import styled, { css } from 'styled-components/macro'
import { style } from "glamor";
import { Fade } from "react-reveal";
import { certifications } from "../../portfolio";
import BackgroundImage from "react-background-image";
import "./Certifications.scss";

const slide = certifications.certifications;

// The slider will fit any size container, lets go full screen...
const AppStyles = styled.main`
  height: 60vh;
  width: 100vw;
  display: flex;
  align-items: center;
  @media (max-width: 768px) {
    height: 30vh;
  }
`

// create some buttons for none drag slide change
const Button = styled.button`
  font-size: 4rem;
  z-index: 2;
  position: relative;
  font-weight: bolder;
  text-align: center;
  height: 7vh;
  width: 7vw;
  display: flex;  
  justify-content: center;  
  align-items: center;  
  background-color: ${(props) => props.theme.body};
  color: ${(props) => props.theme.text};
  border-radius: 25%;
  border-color: ${(props) => props.theme.accentBright};
  cursor: pointer;
  :disabled{
    color: transparent;
    border-color: transparent;
    cursor: auto;
  }
  :hover{
    color: ${(props) => props.theme.accentBright};
    :disabled{
      color: transparent;
    }
  }
  ${(props) =>
    props.right
      ? css`
          margin-right: 10.5rem;
        `
      : css`
          margin-left: 10.5rem;
        `}
  @media (max-width: 768px) {
    font-size: 1.5rem;
    ${(props) =>
      props.right
        ? css`
          margin-right: 0.5rem;
          `
        : css`
          margin-left: 0.5rem;
          `}
  }
`;


// Whatever you render out in the Slider will be draggable 'slides'
function Certifications(props) {

  const theme = props.theme;

  const styles = style({
    backgroundColor: `${theme.accentBright}`,
    ":hover": {
      boxShadow: `0 5px 15px ${theme.accentBright}`,
    },
  });

  // state should start with the index you want to start the slide on
  const [index, setIndex] = useState(1)

  const increment = () => {
    if (index < slide.length - 1) setIndex(index + 1)
  }

  const decrement = () => {
    if (index > 0) setIndex(index - 1)
  }

  return (
    <div>
      <Fade bottom duration={1200} distance="20px">
        <h1 className="experience-heading-text" style={{ color: theme.text }}>
          You can see here my certifications
        </h1>
        <AppStyles>
          <Button onClick={decrement} left disabled={index === 0}>
            <i className="fas fa-arrow-left" ></i>
          </Button>
          <Slider
            onSlideComplete={setIndex}
            onSlideStart={(i) => {
              console.log('started dragging on slide', i)
            }}
            activeIndex={index}
            threshHold={100}
            transition={0.3}
            scaleOnDrag={true}
          > 
            {slide.map(({ image, title, subtitle, certificate_link, }, index) => (
              <div>
                <BackgroundImage
                  placeholder={image}
                  src={image}
                  className=""
                  style={{borderRadius: 20, display: "flex",flexDirection:"column", alignItems: "center", justifyContent: "center"}}
                >
                  <div>
                    <h2 className="slideTitle">{subtitle}</h2>
                  </div>
                  <div>
                    <p className="slideDescription">{}</p>
                  </div>
                  <div style={{cursor:"pointer", }}>
                    <a
                      href={`${certificate_link}`}
                      target="_blank"
                      rel="noopener noreferrer"
                      className="slideButton"
                    >
                      <button {...styles} className="button-certificate">
                        <span style={{ color: theme.dark }}>
                          See <i className="fas fa-eye"></i>
                        </span>
                      </button>
                    </a>
                  </div>
                </BackgroundImage>
              </div>
            ))}
          </Slider>
          <Button onClick={increment} right disabled={index === slide.length - 1}>
            <i className="fas fa-arrow-right"></i>
          </Button>
        </AppStyles>
      </Fade>
    </div>
  )
}

export default Certifications;