import React from "react";
import ProjectCard from "../../components/ProjectCard/ProjectCard";
import { Fade } from "react-reveal";
import { experience } from "../../portfolio.js";
import { socialMediaLinks } from "../../portfolio";
import "./Projects.css";
import ProjectsImg from "./ProjectsImg";
import { style } from "glamor";

function Projects(props) {
  const theme = props.theme;

  const styles = style({
    backgroundColor: `${theme.accentBright}`,
    ":hover": {
      boxShadow: `0 5px 15px ${theme.accentBright}`,
    },
  });

  return (
    <div className="projects-main" id="experience">
      <div className="basic-projects">
        <Fade right duration={1200} distance="40px">
          <div className="projects-heading-div">
            <div className="projects-heading-img-div">
              <ProjectsImg theme={theme} />
            </div>
            <div className="projects-heading-text-div">
              <h1 className="numbered-heading" style={{ color: theme.text, fontSize: 30, marginTop: 60 }}>
                <span style={{ color: theme.accentColor }}>{"<"}</span>
                <span style={{ color: theme.text }}>{"Experience"}</span>
                <span style={{ color: theme.accentColor }}>{"/>"}</span>
              </h1>
              <h1
                className="projects-heading-text"
                style={{ color: theme.text }}
              >
                {experience.title}
              </h1>
              <p
                className="projects-header-detail-text subTitle"
                style={{ color: theme.secondaryText }}
              >
                {experience["description"]}
                <br/>
                {experience["description_down"]}
              </p>
            </div>
          </div>
        </Fade>
      </div>
      <div className="repo-cards-div-main">
        {experience.data.map((repo) => {
          return <ProjectCard repo={repo} theme={theme} />;
        })}
      </div>
      <br />
      <a
        href={`${socialMediaLinks.github}`}
        target="_blank"
        rel="noopener noreferrer"
      >
        <button {...styles} className="button">
          <span style={{ color: theme.dark }}>
            More Projects <i className="fab fa-github" ></i>
          </span>
        </button>
      </a>
      <br />
      <br />
    </div>
  );
}

export default Projects;
