import React from "react";
import ProjectLanguages from "../projectLanguages/ProjectLanguages";
import "./ProjectCard.css";
import { Fade } from "react-reveal";
import { style } from "glamor";

export default function ProjectCard({ repo, theme }) {
  function openRepoinNewTab(url) {
    var win = window.open(url, "_blank");
    win.focus();
  }

  const styles = style({
    color: "rgb(88, 96, 105)",
    backgroundColor: "rgb(255, 255, 255)",
    boxShadow: "rgba(0, 0, 0, 0.2) 0px 10px 30px -15px",
    padding: "2rem",
    cursor: "pointer",
    borderRadius: "5px",
    height: "100%",
    transition: "all 0.2s ease-in-out",
    ":hover": {
      boxShadow: `${theme.accentColor} 0 1px 15px`,
    },
  });

  return (
    <div>
      <Fade bottom duration={300} distance="40px">
        <div
          {...styles}
          onClick={() => { 
            if (repo.url !== "") {
              openRepoinNewTab(repo.url);
            }
          }}
          style={{ backgroundColor: theme.projectCard }}
        >
          <div className="repo-name-div">
            <p className="repo-name" style={{ color: theme.text }}>
              {repo.name}
              {
                repo.playstore === "" ? null : (
                  <a href={repo.playstore} className="icon-button gitlab" rel="noopener noreferrer" target="_blank">
                    <i className="fab fa-google" ></i>
                  </a>
                )
              }
              {
                repo.appstore === "" ? null : (
                  <a href={repo.appstore} className="icon-button gitlab" rel="noopener noreferrer" target="_blank">
                    <i className="fab fa-apple" ></i>
                  </a>
                )
              }
              {
                repo.webstore === "" ? null : (
                  <a href={repo.webstore} className="icon-button gitlab" rel="noopener noreferrer" target="_blank">
                    <i className="fab fa-chrome" ></i>
                  </a>
                )
              }
              {
                repo.url === "" ? null : (
                  <a href={repo.url} className="icon-button gitlab" rel="noopener noreferrer" target="_blank">
                    <i className="fab fa-github" ></i>
                  </a>
                )
              }
            </p>
          </div>
          <p className="repo-description" style={{ color: theme.text }} dangerouslySetInnerHTML={{ __html: repo.description }} />
            
          <div className="repo-details">
            <ProjectLanguages logos={repo.languages} />
          </div>
          {
            (repo.video === "") ? 
              (repo.pic === "") ? 
                null
                : 
                (
                  <div className="">
                    <img src={require(`../../assests/images/experience/${repo.pic}`)} width="100%" height="300" alt="project pic" className="" />
                  </div>
                )
             : 
            (
              <div className="">
                <iframe width="100%" height="300" src={repo.video} title="YouTube video player" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
              </div>
            )
          }
        </div>
      </Fade>
    </div>
  );
}
