import React, { useState, useEffect } from "react";
import "./Header.css";
import { greeting, settings } from "../../portfolio";
import { CgSun } from "react-icons/cg/";
import { HiMoon } from "react-icons/hi";
import { style } from "glamor";
import { Link } from "react-scroll";
import { NavLink } from "react-router-dom";

function Header(props) {
  const theme = props.theme;
  const styles = style({
    cursor: "pointer",
    height: "45px",
    width: "45px",
    marginRight: "5px",
    marginLeft: "15px",
    paddingTop: "5px",
    borderRadius: "50%",
    border: "none",
    alignItems: "center",
    justifyContent: "center",
    backgroundColor: props.theme.name === "light" ? "#292C3F" : "#ffbd39",
    outline: "none",
    transition: "all 0.2s ease-in-out",
    ":hover": {
      boxShadow: `0 3px 8px ${
        props.theme.name === "light" ? "#F7D774" : "#646464"
      }`,
    },
  });

  const link = settings.isSplash ? "/splash" : "home";

  const [currTheme, setCurrTheme] = useState(props.theme);

  function changeTheme() {
    if (currTheme === "light") {
      props.setTheme("dark");
      localStorage.setItem("theme", "dark");
      setCurrTheme("dark");
    } else {
      props.setTheme("light");
      localStorage.setItem("theme", "light");
      setCurrTheme("light");
    }
  }

  const icon =
    props.theme.name === "dark" ? (
      <HiMoon
        strokeWidth={1}
        size={20}
        color={props.theme.name === "light" ? "#FFF" : "#292C3F"}
      />
    ) : (
      <CgSun
        strokeWidth={1}
        size={20}
        color={props.theme.name === "light" ? "#ffbd39" : "#ffbd39"}
      />
    );

  // Sticky Menu Area
  useEffect(() => {
      window.addEventListener('scroll', isSticky);
      return () => {
          window.removeEventListener('scroll', isSticky);
      };
  });

       
  /* Method that will fix header after a specific scrollable */
  const isSticky = (e) => {
      const header = document.querySelector('.header');
      const scrollTop = window.scrollY;
      scrollTop >= 10 ? header.classList.add('is-sticky') : header.classList.remove('is-sticky');
  };


  return (
    <header className="header" style={{ backgroundColor: theme.flip }} >
      <NavLink to={link} tag={Link} className="logo" style={{ color: theme.text, cursor: "pointer" }}>
        <span className="logo-name"  style={{ color: theme.text, cursor: "pointer" }}>
          <span style={{ color: theme.accentColor }}>{"<"}</span>
          {greeting.logo_name}
          <span style={{ color: theme.accentColor }}>{"/>"}</span>
        </span>
      </NavLink>
      <input className="menu-btn" type="checkbox" id="menu-btn" />
      <label className="menu-icon" htmlFor="menu-btn">
        <span className="navicon"></span>
      </label>
      <ul className="menu">
        <li>
          <Link
            className="homei"
            to="about"
            activestyle={{ fontWeight: "bold" }}
            style={{ borderRadius: 5, color: theme.text, cursor: "pointer" }}
            activeClass="active"
            spy={true}
            smooth={true}
            offset={-500}
            duration={500}
          >
           <span>{"About "}<i className="fas fa-address-card"></i></span>
          </Link>
        </li>
        <li>
          <Link
            className="ec"
            to="background"
            activeStyle={{ fontWeight: "bold" }}
            style={{ borderRadius: 5, color: theme.text, cursor: "pointer" }}
            activeClass="active"
            spy={true}
            smooth={true}
            offset={-300}
            duration={500}
          >
            <span>{"Background "}<i className="fas fa-graduation-cap"></i></span>
          </Link>
        </li>
        <li>
          <Link
            className="projects"
            to="experience"
            activeStyle={{ fontWeight: "bold" }}
            style={{ borderRadius: 5, color: theme.text, cursor: "pointer" }}
            activeClass="active"
            spy={true}
            smooth={true}
            offset={-150}
            duration={500}
          >
            <span>{"Experience "}<i className="fas fa-laptop-code"></i></span>
          </Link>
        </li>
        <li>
          <a
            className="resume"
            href={greeting.resumeLink}
            target="_blank"
            rel="noopener noreferrer"
            activeStyle={{ fontWeight: "bold" }}
            style={{ borderRadius: 5, color: theme.text }}
          >
            <span> Resume <i className="fas fa-print"></i></span>
          </a>
        </li>
        <button {...styles} onClick={changeTheme}>
          {icon}
        </button>
      </ul>
    </header>
  );
}

export default Header;
