import React, { useState, useEffect, useRef } from 'react';
import { CSSTransition } from 'react-transition-group';
import styled, { css } from 'styled-components';
import { background } from '../../portfolio';
import { Fade } from "react-reveal";

const mixins = {

  inlineLink: css`
    display: inline-block;
    text-decoration: none;
    text-decoration-skip-ink: auto;
    position: relative;
    transition: var(--transition);
    color: var(--green);
    &:hover,
    &:focus,
    &:active {
      color: var(--green);
      outline: 0;
      &:after {
        width: 100%;
      }
      & > * {
        color: var(--green) !important;
        transition: var(--transition);
      }
    }
    &:after {
      content: '';
      display: block;
      width: 0;
      height: 3px;
      position: relative;
      bottom: 0em;
      background-color: var(--green);
      transition: var(--transition);
      opacity: 0.5;
    }
  `,

  fancyList: css`
    padding: 0;
    margin: 0;
    list-style: none;
    font-size: var(--fz-lg);
    li {
      position: relative;
      padding-left: 30px;
      margin-bottom: 10px;
      &:before {
        content: '';
        position: absolute;
        left: 0;
        color: var(--green);
      }
    }
  `,
  
};

const StyledJobsSection = styled.section`
  
  .inner {
    display: flex;

    @media (max-width: 768px) {
      display: block;
    }

    // Prevent container from jumping
    @media (min-width: 700px) {
      min-height: 340px;
    }
  }
  
  .inline-link {
    ${({ theme }) => mixins.inlineLink};
  }
`;

const StyledTabList = styled.div`
  position: relative;
  z-index: 3;
  width: max-content;
  padding: 0;
  margin: 0;
  margin-top: 0px;
  list-style: none;

  @media (min-width: 768px) {
    width: 400px;
  }

  @media (max-width: 768px) {
    display: flex;
    overflow-x: auto;
    width: 87vw;
    margin-left: 0px;
    margin-bottom: -30px;
  }
  @media (max-width: 480px) {
    width: 87vw;
  }

  li {
    &:first-of-type {
      @media (max-width: 768px) {
        margin-left: 50px;
      }
      @media (max-width: 480px) {
        margin-left: 25px;
      }
    }
    &:last-of-type {
      @media (max-width: 768px) {
        padding-right: 50px;
      }
      @media (max-width: 480px) {
        padding-right: 25px;
      }
    }
  }
`;

const StyledTabListDiv = styled.div`
  display: block;
  @media (max-width: 768px) {
    display: flex;
  }
`;

const StyledHead = styled.h1`
  text-align: center;
  @media (max-width: 768px) {
    text-align: left;
  }
`;

const StyledTabButton = styled.button`

  border-radius: var(--border-radius);
  border: 1px solid ${({ isActive }) => (isActive ? 'var(--green)' : 'var(--lightest-navy)')};
  background-color: transparent;
  color: ${({ isActive }) => (isActive ? 'var(--green)' : 'var(--slate)')};
  font-family: var(--font-mono);
  font-size: var(--fz-xs);
  text-align: left;
  cursor: pointer;
  height: var(--tab-height);
  white-space: nowrap;
  padding: 0 15px;
  width: 100%;

  @media (min-width: 768px) {
    align-items: center;
    margin-bottom: 5px;
    border-left: ${({ isActive }) => (isActive ? '7px' : '1px')} solid ${({ isActive }) => (isActive ? 'var(--green)' : 'var(--lightest-navy)')};
  }

  @media (max-width: 768px) {
    align-items: center;
    margin-right: 5px;
    border-bottom: ${({ isActive }) => (isActive ? '7px' : '1px')} solid ${({ isActive }) => (isActive ? 'var(--green)' : 'var(--lightest-navy)')};
  }

  &:hover,
  &:focus {
    background-color: var(--light-navy);
  }
`;

const StyledTabPanels = styled.div`
  position: relative;
  width: 100%;
  margin-left: 20px;

  @media (max-width: 768px) {
    margin-left: 0;
  }
`;

const StyledTabPanel = styled.div`
  width: 100%;
  height: auto;
  padding: 20px 5px;

  ul {
    ${({ theme }) => mixins.fancyList};
  }

  h3 {
    margin-bottom: 0px;
    font-size: var(--fz-xxl);
    font-weight: 500;
    line-height: 1.3;

    .company {
      color: var(--green);
    }
  }

  .range {
    margin-bottom: 20px;
    color: var(--light-slate);
    font-family: var(--font-mono);
    font-size: var(--fz-xs);
  }
`;

const Jobs = () => {

  const [activeTabId, setActiveTabId] = useState(0);
  const [tabFocus, setTabFocus] = useState(null);
  const tabs = useRef([]);

  // Only re-run the effect if tabFocus changes
  useEffect(() => {
    const focusTab = () => {
      if (tabs.current[tabFocus]) {
        tabs.current[tabFocus].focus();
        return;
      }
      // If we're at the end, go to the start
      if (tabFocus >= tabs.current.length) {
        setTabFocus(0);
      }
      // If we're at the start, move to the end
      if (tabFocus < 0) {
        setTabFocus(tabs.current.length - 1);
      }
    };
    focusTab();
  }, [tabFocus]);

  return (
    <StyledJobsSection id="jobs" ref={{}}>
      <div className="inner">
        <Fade left duration={1200} distance="40px">
          <StyledTabList role="tablist" aria-label="Job tabs" >
              <div style={{ borderColor:"#7D6AAD", padding:10, borderStyle:"solid", borderWidth:2, borderRadius:5, marginRight:5, marginBottom:5 }}>
                <StyledHead style={{ color:"#7D6AAD", }}><i className="fas fa-briefcase"></i>{" " + background.title[0]}</StyledHead>
                <StyledTabListDiv>
                  {
                    background && background.work.map(({ company_sigle }, i) => {
                      return (
                        <StyledTabButton
                          key={i}
                          isActive={activeTabId === i}
                          onClick={() => setActiveTabId(i)}
                          ref={el => (tabs.current[i] = el)}
                          id={`tab-${i}`}
                          role="tab"
                          tabIndex={activeTabId === i ? '0' : '-1'}
                          aria-selected={activeTabId === i ? true : false}
                          aria-controls={`panel-${i}`}>
                          <span>{(i+1)+". "+company_sigle}</span>
                        </StyledTabButton>
                      );
                    })
                  }
                </StyledTabListDiv>
              </div>
              <div style={{ borderColor:"#CC583F", padding:10, borderStyle:"solid", borderWidth:2, borderRadius:5, marginRight:5, marginBottom:5 }}>
                <StyledHead style={{ color:"#CC583F", }}><i className="fas fa-graduation-cap"></i>{" " + background.title[1]}</StyledHead>
                <StyledTabListDiv>
                {
                  background && background.education.map(({ company_sigle }, i) => {
                    return (
                      <StyledTabButton
                        key={(i + background.work.length)}
                        isActive={activeTabId === (i + background.work.length)}
                        onClick={() => setActiveTabId((i + background.work.length))}
                        ref={el => (tabs.current[(i + background.work.length)] = el)}
                        id={`tab-${(i + background.work.length)}`}
                        role="tab"
                        tabIndex={activeTabId === (i + background.work.length) ? '0' : '-1'}
                        aria-selected={activeTabId === (i + background.work.length) ? true : false}
                        aria-controls={`panel-${(i + background.work.length)}`}>
                        <span>{(i+1)+". "+company_sigle}</span>
                      </StyledTabButton>
                    );
                  })
                }
                </StyledTabListDiv>
              </div>
              <div style={{ borderColor:"#75C095", padding:10, borderStyle:"solid", borderWidth:2, borderRadius:5, marginRight:5, marginBottom:5 }}>
                <StyledHead style={{ color:"#75C095", }}><i className="fas fa-hand-holding-heart"></i>{" " + background.title[2]}</StyledHead>
                <StyledTabListDiv>
                  {
                    background && background.volunteership.map(({ company_sigle }, i) => {
                      return (
                        <StyledTabButton
                          key={(i + background.work.length + background.education.length)}
                          isActive={activeTabId === (i + background.work.length + background.education.length)}
                          onClick={() => setActiveTabId((i + background.work.length + background.education.length))}
                          ref={el => (tabs.current[(i + background.work.length + background.education.length)] = el)}
                          id={`tab-${(i + background.work.length + background.education.length)}`}
                          role="tab"
                          tabIndex={activeTabId === (i + background.work.length + background.education.length) ? '0' : '-1'}
                          aria-selected={activeTabId === (i + background.work.length + background.education.length) ? true : false}
                          aria-controls={`panel-${(i + background.work.length + background.education.length)}`}>
                          <span>{(i+1)+". "+company_sigle}</span>
                        </StyledTabButton>
                      );
                    })
                  }
                </StyledTabListDiv>
              </div>
            {/* 
              <StyledHighlight activeTabId={activeTabId} />
            */}
          </StyledTabList>
        </Fade>
        <Fade right duration={1200} distance="40px">
          <StyledTabPanels>
            {background &&
              background.work.concat(background.education).concat(background.volunteership)
              .map(({ title, company, location, url, url_location, range, pic, description }, i) => {
                return (
                  <CSSTransition key={i} in={activeTabId === i} timeout={250} classNames="fade">
                    <StyledTabPanel
                      id={`panel-${i}`}
                      role="tabpanel"
                      tabIndex={activeTabId === i ? '0' : '-1'}
                      aria-labelledby={`tab-${i}`}
                      aria-hidden={activeTabId !== i}
                      hidden={activeTabId !== i}>
                      <h3>
                        <span>{title}</span>
                        <span className="company">
                          &nbsp;@&nbsp;
                          <a href={url} target="_blank" rel="noopener noreferrer" className="inline-link">
                            {company}
                          </a>
                        </span>
                      </h3>
                      <p className="range">{range + " ("}
                        <span style={{color: "#ffbd39"}}>
                          <a href={url_location} target="_blank" rel="noopener noreferrer" className="inline-link">
                              {location}
                          </a>
                        </span>{")"}
                      </p>
                      <a href={url} target="_blank" rel="noopener noreferrer" style={{ maxWidth:200, maxHeight:100 }} className="">
                        <img style={{ marginLeft:'auto', marginRight:'auto', borderRadius:10 }} alt={company} width='100%' height='100%' src={require(`../../assests/images/background/${pic}`)} />
                      </a>
                      <div dangerouslySetInnerHTML={{ __html: description }} />
                    </StyledTabPanel>
                  </CSSTransition>
                );
              })}
          </StyledTabPanels>
        </Fade>
      </div>
    </StyledJobsSection>
  );
};

export default Jobs;
